import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import SwiperCore, {Navigation, Pagination} from 'swiper';
import 'swiper/swiper-bundle.css'
import { FeedbacksContainer, SwiperContainer, ImgWrapper, FeedbacksH1 } from './Feedbacks';
import Slide1 from './Feedback1.JPG';
import Slide2 from './Feedback2.JPG';
import Slide3 from './Feedback3.JPG';
import Slide4 from './Feedback4.jpeg';
import Slide5 from './Feedback5.JPG';
// import Slide6 from './Feedback6.jpeg';
import './Feedbacks.css'

SwiperCore.use([Navigation, Pagination]);

const Feedbacks = () => {

    const images = [Slide1, Slide2, Slide3, Slide4, Slide5];
    const names = ['Luis Guilherme', 'Kauee Bomfim', 'Guilherme Dandolini', 'Wanderson Mourão', 'Anna Matos']

    const slides = [];

    for(let j = 0; j < 10; j++)
    {
        for(let i = 0; i < 5; i++)
        {
            slides.push(
                <SwiperSlide className='slide' tag='li' key={`slide-${i}`} 
                style={{listStyle: 'none', width: '100%', height: 'auto'}}
                >
                    <h1>{names[i]}</h1>
                    <ImgWrapper>
                        <img id='feedback' src={images[i]} alt={`slide-${i}`} 
                        // width='100%' height='auto' style={{borderRadius: '5px solid'}}
                        />
                    </ImgWrapper>
                </SwiperSlide>
            )
        }   
    }
     
    


    return (
           
        <FeedbacksContainer>
            <FeedbacksH1>FEEDBACKS</FeedbacksH1>
        <React.Fragment>
            {/* <SwiperContainer> */}        
            <Swiper tag='section' wrapperTag='ul' id='main' navigation pagination spaceBetween={0} slidesPerView={1}>{slides}</Swiper>    
            {/* </SwiperContainer>              */}
        </React.Fragment>  
        </FeedbacksContainer>
      
    )
}

export default Feedbacks;