import React from 'react'
// import { FormWrapp, Box, Form } from './FormComponents'
// import { useForm } from './useForm'
// import validation from './validation'
import '../Form/Form.css'
import validate from './validate'

const FormTest = ({onInputChange, onSubmit, handleSubmit, errors, selectRadio,submitted}) => {

    // const { handleChange, values, handleSubmit, errors } = useForm(submitForm, validation);
    validate()
    
    return (
        <div id='form' className='form-container'>
            <div className='form-content'>
                <h1 className='waitlist'>ENTRE NA LISTA DE ESPERA DA MENTORIA</h1>
                <form className='form' onSubmit={handleSubmit}>
                    <div className='form-inputs'>
                        <label htmlFor='number' className='form-label'>Qual o @ do Instagram ou Whatsapp para contato?</label>
                        <input 
                            name='number' 
                            className='form-input' 
                            id='number' 
                            placeholder='Instagram/Whatsapp' 
                            type='text' 
                            // onChange={handleChange}
                                
                                
                            // value={'values.number'}
                                
                            onChange={onInputChange}
                        />
                        {errors.number && <p className='error'>{errors.number}</p>}                            
                    </div>
                    <div className='form-inputs'>
                        <label htmlFor='instagram' className='form-label'>Deseja apenas colocar seu nome na lista? Ou quer marcar uma reunião para te ajudarmos?</label>
                        <div className='input-wrapp'>
                            <input 
                                name='option' 
                                // className='form-input' 
                                // id='instagram' 
                                // placeholder='Usuário do Instagram' 
                                onChange={selectRadio} 
                                type='radio'
                                value='nome'
                            />
                            <p className='select-p'>Apenas quero colocar meu nome e ser avisado</p>                            
                        </div>
                        <div className='input-wrapp'>
                            <input onChange={selectRadio} name='option' type='radio' value='reuniao'/>
                            <p className='select-p'>Quero colocar meu nome e marcar uma reunião</p>                            
                        </div>

                        {errors.selected && <p className='error'>{errors.selected}</p>}                            
                    </div>
                    <button className='form-button' type='submit' onClick={onSubmit}>Confirmar</button> 
                    {submitted && <p className='sent'>Enviado</p>}        
                </form>      
            </div>
        </div>
 
    )
}

export default FormTest

